import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";

const PaymentPage = () => {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);

  const participantName = queryParams.get("name") || "Guest";
  const amount = queryParams.get("amount") || "0";
  const hostUpiId = queryParams.get("upi") || "";
  const transactionId = `${participantName}-${amount}-${hostUpiId}`;

  const upiLink = `upi://pay?pa=${hostUpiId}&pn=${encodeURIComponent(participantName)}&am=${amount}&cu=INR`;

  // ✅ State for payment success
  const [paymentSuccess, setPaymentSuccess] = useState(
    localStorage.getItem(transactionId) === "paid"
  );

  // ✅ State to show a custom modal instead of using `confirm()`
  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    const handleVisibilityChange = () => {
      if (document.visibilityState === "visible" && !paymentSuccess) {
        setShowModal(true); // ✅ Open confirmation modal when returning to page
      }
    };

    document.addEventListener("visibilitychange", handleVisibilityChange);
    return () => document.removeEventListener("visibilitychange", handleVisibilityChange);
  }, [paymentSuccess]);

  const handleConfirmPayment = () => {
    localStorage.setItem(transactionId, "paid");
    setPaymentSuccess(true);
    setShowModal(false);
  };

  return (
    <div className="container">
      <div className="card">
        <h2 className="text-xl font-bold">Payment Details</h2>
        <p><strong>Name:</strong> {participantName}</p>
        <p><strong>Amount to Pay:</strong> ₹{amount}</p>
        <p><strong>Host UPI ID:</strong> {hostUpiId}</p>

        {paymentSuccess ? (
          <p className="success-message">✅ Payment Successful!</p>
        ) : (
          <button className="button" onClick={() => window.location.href = upiLink}>
            Pay Now
          </button>
        )}
      </div>

      {/* ✅ Custom Modal for Payment Confirmation */}
      {showModal && (
        <div className="modal">
          <div className="modal-content">
            <h3>Confirm Payment</h3>
            <p>Did you complete the payment?</p>
            <button className="button small" onClick={handleConfirmPayment}>Yes, I Paid</button>
            <button className="button small cancel" onClick={() => setShowModal(false)}>Cancel</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default PaymentPage;
